@import url('../font/pretendard.css');
@import url('../font/ArchivoBlack.css');
@import url('../font/NotoSansCJKkr.css');

#main_page {background:#FAFAFA; color:#111111; overflow-y: hidden;}
#main_page * {font-family: 'pretendard';}
#main_page .sc_tit {padding:1.5rem 0; font-size:3.5rem; font-weight: 600; letter-spacing: -0.1rem;}
.section {position:relative; height:100vh; background:#FAFAFA; z-index:6;}
.con {max-width:140rem; margin:0 auto;}
.con2 {max-width:114rem; margin:0 auto;}
.page_tit {display: flex; justify-content: center;}
.txt1 {font-size:3.1rem;}
.txt2 {font-size:3.2rem;}
.txt3 {font-size:3.5rem; font-weight:900;}
.hide {opacity: 0; transform:translateY(-1rem); transition: opacity 0.2s, transform 0.2s;}
.show {opacity: 1; transform:translateY(0); transition: opacity 0.2s, transform 0.2s;}
.lodigng_txt {display: none;}
.lodigng_txt > div {text-align: center; font-size:2rem; letter-spacing: -0.1rem;}

/* select */
.loding_page {display:flex; justify-content:center; align-items:center; position:fixed; width: 100%; height: 100vh; background: #efefef; z-index: 999;}
.lds-ripple {display: inline-block; position: relative; width: 12rem; height: 12rem;}
.lds-ripple div {position: absolute; border: 4px solid #000000; opacity: 1; border-radius: 50%; animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;}
.lds-ripple div:nth-child(2) {animation-delay: -0.5s;}
@keyframes lds-ripple {
    0% {
        top: 50%;
        left: 50%;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
    }
}
      


/* Header */
.main_header {width:100vw; z-index: 10;}
.main_header ul li {padding:1.5rem 2rem; margin-right:2rem; }
.main_header ul li span {font-size:1.5rem; cursor:pointer;}
.main_header .section_progress {height: 0.5rem; background:#eeeeee;}
.y_Top ul {height:7rem; background:transparent; transition:height 0.3s, background 0.3s;}
.y_move ul {height:6rem; background:#757575; transition:height 0.3s, background 0.3s;}
.y_move ul li span {color:#eeeeee;}
.y_move ul li svg {fill:#eeeeee;}

/* section_01 */
#section_01 {padding-top:8rem; position: relative;}
#section_01 .sc_01_bg {width:0; height:100%; top:0; left:0; background:#ffffff;}
#section_01 .main_deco {margin-top:8rem;}
#section_01 .main_deco > div {font-size:10rem; font-family:'ArchivoBlack'; color:#222222; text-shadow:1px 0.1rem #ffffff, 0 0.1rem #ffffff, -0.1rem -0.1rem #ffffff, -0.1rem 0 #ffffff; cursor:default;}
/* #section_01 .sc_bg_1 {width:100%; height:100%; top:0; right:0; z-index: 4; background:url('../img/000027.JPG'); background-size:cover; z-index: -1;} */

/* section_02 */
#section_02 {z-index:3;}
#section_02::after {content:''; position:absolute; width:20rem; height: calc(100% + 10rem); top:0; left:25%; background:#fafafa; z-index: 5; transform:rotate(-10deg) translateY(-5rem);}
#section_02 .page_tit {display:flex; flex-direction:column; position:absolute; width:30%; right:10%; margin-top: 8rem;}
#section_02 .page_tit span {justify-content:center; align-items:flex-end; margin-right:3rem; font-weight:400;}
#section_02 .page_tit > span:not(:first-child) {margin-top:1rem;}
#section_02 .page_tit span div {margin:0 1rem;}
#section_02 .cont_box {justify-content:space-between;}
#section_02 .cont_box .sc_tit {flex-basis:100%;}
#section_02 .cont_box .img_box {width:30%; height:100%; top:0; left:0; z-index: 4; background:url('../img/profil.JPG'); background-size:cover; transition:background 0.3s;}
#section_02 .cont_box .img_box img {width:40rem;}
#section_02 .cont_box .txt_box {margin-left:25%; padding-top:21rem;}
#section_02 .cont_box .txt_box.intro {display:flex; width:100%; flex-direction: column; justify-content:flex-start; align-items: flex-start;}
#section_02 .cont_box .txt_box.intro .sub_tit {font-size:2.5rem; padding-left:10rem; padding-bottom:1.5rem; margin-top:10rem; transition: padding 0.3s, margin 0.3s;}
#section_02 .cont_box .txt_box.intro ul {width:100%; text-align: left;}
#section_02 .cont_box .txt_box.intro ul li {font-size:1.7rem; line-height:2.2;}
#section_02 .cont_box .txt_box.intro ul li > span {flex-basis:16rem; margin-right:2rem; font-size:1.7rem; font-weight:600;}

/* section_03 */
#section_03 {padding-top:8rem;}
#section_03 .cont_box .sc_tit {flex-basis:100%; margin-bottom:5rem;}
#section_03 .cont_box .web_work {max-width: 80%; justify-content: flex-start; margin:0 auto;}
#section_03 .cont_box .web_work li {display:flex; justify-content:center; flex-basis:calc(100%/4); margin-bottom:10rem;  transition:flex-basis 0.3s, opacity 0.3s, visible 0.3s;}
/* #section_03 .cont_box .web_work li:not(:nth-child(4n + 5)) {margin-right:2rem;} */
#section_03 .cont_box .web_work li a {position:relative;}
#section_03 .cont_box .web_work li .link_btn {position:relative; width:20rem; height:16.5rem; border-radius:1rem; background: url('../img/mockup_pc.png'); background-size: cover; transition:background 0.3s;}
#section_03 .cont_box .web_work li .link_btn:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_btn::after {transition:all 1s;}
#section_03 .cont_box .web_work li .link_btn.reserve::after {content:''; position:absolute; z-index: -1; background:url('../img/reserve_main.png'); width:93%; height: 67%; top:5%; left:4%; background-size:cover; background-repeat: no-repeat;}
#section_03 .cont_box .web_work li .link_btn.reserve:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_btn.clean::after {content:''; position:absolute; z-index: -1; background:url('../img/clean_main.png'); width:95%; height: 75%; top:5%; left:4%; background-size:cover; background-repeat: no-repeat;}
#section_03 .cont_box .web_work li .link_btn.clean:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_btn.acrc::after {content:''; position:absolute; z-index: -1; background:url('../img/acrc_main.png'); width:95%; height: 75%; top:5%; left:4%; background-size:cover; background-repeat: no-repeat;}
#section_03 .cont_box .web_work li .link_btn.acrc:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_btn.acrc_child::after {content:''; position:absolute; z-index: -1; background:url('../img/acrc_child_main.png'); width:95%; height: 75%; top:5%; left:4%; background-size:cover; background-repeat: no-repeat;}
#section_03 .cont_box .web_work li .link_btn.acrc_child:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_btn.prtl::after {content:''; position:absolute; z-index: -1; background:url('../img/prtl_main.gif'); width:95%; height: 65%; top:5%; left:1%; background-size:cover; background-repeat: no-repeat;}
#section_03 .cont_box .web_work li .link_btn.prtl:hover::after {background-position-Y:bottom;}
#section_03 .cont_box .web_work li .link_txt {position:absolute; width: 100%; margin-top:0.8rem; text-align:center; word-break: break-word;}
 
/* section_04 */
#section_04 {height: auto;}
#section_04 .sc_tit {flex-basis:100%;}
#section_04 .cont_box .sub_tit {display:flex; justify-content:center; margin-bottom:3rem;}
#section_04 .cont_box .txt_box {width:100%; margin-bottom:2rem;}
#section_04 .cont_box .txt_box ul li {flex-basis:100%; font-size:2rem;}
#section_04 .contect_me_form {flex-basis:70%;}
#section_04 .contect_me_form form {display:flex; flex-wrap: wrap;}
#section_04 .contect_me_form form > div {flex-basis:100%;}
#section_04 .contect_me_form form .contect_header > div {flex-basis: 50%; padding:1rem;}
#section_04 .contect_me_form form input[name="name"] {width:100%; font-size: 1.6rem; padding:0.5rem 1rem;}
#section_04 .contect_me_form form input[name="email"] {width:100%; font-size: 1.6rem; padding:0.5rem 1rem;}
#section_04 .contect_me_form form .email_contents_box {padding:0 1rem 1rem 1rem;}
#section_04 .contect_me_form form textarea {width:100%; height:15em; font-size:1.6rem; padding:0.5rem 1rem; resize:none;}
#section_04 .contect_me_form form .contect_btn input[type="submit"] {padding:1rem 1.5rem; border-radius:0.5rem; border:0.1rem solid #222222; background:#ffffff; font-size:1.7rem; color:#222222; transition:all 0.3s; cursor:pointer;}
#section_04 .contect_me_form form .contect_btn input[type="submit"]:focus,
#section_04 .contect_me_form form .contect_btn input[type="submit"]:hover {color:#ffffff; background:#222222;}
#section_04 .contect_me_form form .contect_btn input[type="submit"]:active {color:#ffffff; background:#222222; box-shadow: -2px 0px 5px #373737; transform: scale(0.95);}

#section_04 .contect_me_form form .contect_header  {height:8rem;}
#section_04 .contect_me_form form .email_contents_box {height:27rem;}

#section_04 .link_list {flex-basis:100%; padding-top:3rem; padding-bottom:5rem;}
#section_04 .link_list ul li {flex-basis:15rem; margin:1rem; position:relative;}
/* #section_04 .link_list ul .send_mail {margin-top:3rem;} */
#section_04 .link_list ul li:hover {justify-content: center;}
#section_04 .link_list ul li a {position:relative; display:flex; justify-content:center; align-items:center; width:100%; height:14rem; border:0.1rem solid #222222; border-radius:1rem; background:#ffffff; transition:background 0.2s, width 0.2s 0.3s, height 0.2s 0.5s; z-index: 2;}
#section_04 .link_list ul li a::after {position:absolute; display: flex; justify-content: center; align-items: flex-end; width:100%;  height:100%; top:0; left:0; font-size:1.8rem; color:#2c2a29; letter-spacing: -0.1rem; opacity:0; transition: opacity 0.3s, top 0.3s; z-index: -1;}
#section_04 .link_list ul li a svg {width:7.5rem; height:7.5rem;}
#section_04 .link_list ul .send_mail a::after {content:' ';}
#section_04 .link_list ul .send_mail a .svg_box {background:url('../img/icon/gmail.svg') center center no-repeat; background-size: 7.5rem 7.5rem; width: 7.5rem; height: 7.5rem; transition: background 0.3s;}
#section_04 .link_list ul .send_mail a:hover .svg_box {background:url('../img/icon/gmail_color.svg') center center no-repeat; background-size: 7.5rem 7.5rem; width: 7.5rem; height: 7.5rem;}
#section_04 .link_list ul .send_mail a:hover::after {top:20%; opacity: 1;}
#section_04 .link_list ul .git_blog a svg {width: 55px;}
#section_04 .link_list ul .send_mail:hover::after {opacity:1;}
#section_04 .link_list ul .opentok a::after {content:' ';}
#section_04 .link_list ul .opentok:hover::after {opacity:1;}
#section_04 .link_list ul .opentok a:hover {background:#FAE100;}
#section_04 .link_list ul .opentok a:hover svg {fill:#3C1E1E;}
#section_04 .link_list ul .opentok a:hover::after {top:20%; opacity: 1;}
#section_04 .link_list ul .github a::after {content:'Github';}
#section_04 .link_list ul .github a:hover {background:#222222;}
#section_04 .link_list ul .github a:hover svg {fill:#ffffff;}
#section_04 .link_list ul .github a:hover::after {top:20%; opacity: 1;}
#section_04 .link_list ul .git_blog a::after {content:'Jekyll Blog';}
#section_04 .link_list ul .git_blog a:hover {background: #2b2b2b;}
#section_04 .link_list ul .git_blog a:hover svg {fill:#ffffff;}
#section_04 .link_list ul .git_blog a:hover::after {top:20%; opacity: 1;}
#section_04 .cont_box .page_copyright {font-size:2rem; margin:0 auto 1rem auto;}

