/* footer */
.main_footer {position:relative; height:auto; padding:2rem 0 0rem; background:#fafafa; border-top:0.1rem solid #757575;}
.main_footer .copyright {font-size:1.3rem; font-family:'NotoKr' !important; font-weight: 300; margin-bottom:2rem;}
.main_footer .link_box ul li {margin:0 0.5rem; border-radius:0.3rem; border:0.1rem solid #222222;}
.main_footer .link_box ul li a {display: flex; justify-content:center; align-items:center; width:3rem; height:3rem;}
.main_footer .link_box ul li svg {width:2rem; height:2rem;}
.main_footer .link_box ul .github a:hover {background:#161B22;}
.main_footer .link_box ul .github a:hover svg {fill:#ffffff;}
.main_footer .link_box ul .gitblog a:hover {background: #222222;}
.main_footer .link_box ul .gitblog a:hover svg {fill:#ffffff;}

/* sub_header */
.go_home {position: fixed; display: flex; justify-content: flex-start; align-items: center; width: 100%; height: 8rem; background: #fefefe; z-index: 2; transition: all 0.3s;}
.go_home a {padding: 1rem; font-size: 1.5rem; font-weight: 600; color:#222222; z-index: 10;}
.go_home a svg {font-size: 4rem;}
.go_home.y_move {height: 6rem; background: #9E9E9E;}
.go_home.y_move a { z-index: 20;}
.go_home.y_move a svg {fill:#ffffff;}

.go_home .progress {height: 8rem; background: #BDBDBD; transition: height 0.3s;}
.go_home.y_move .progress {height: 6rem; background: #757575;}


/* portfolio_info */
.con2 {max-width: 100rem;}
.portfolio_info .cont_box ul li {letter-spacing: -0.14rem;}
.con2 .page_tit {margin-bottom:2rem; padding-top:8rem; font-size:3.5rem; font-weight:800;}
.portfolio_info .cont_1 .sub_tit {margin-bottom:2rem; font-size: 2.4rem; font-weight:600;}
.portfolio_info .cont_1 .sub_txt {border:0.1rem solid #dddddd; padding:1rem; margin-bottom:3rem;}
.portfolio_info .cont_1 .sub_txt p {font-size:1.7rem; line-height: 2.4rem; word-break: keep-all;}
.portfolio_info .cont_2 .txt_box > * {flex-basis:100%;}
.portfolio_info .cont_2 .txt_box .percent li {line-height:2.5rem;}
.portfolio_info .cont_2 .txt_box h2,.portfolio_info .cont_2 .txt_box h2 span {margin-bottom:1rem; font-size:2.1rem;}
.portfolio_info .cont_2 .txt_box .dots_b {position:relative; margin-left: 1rem; line-height: 2rem;}
.portfolio_info .cont_2 .txt_box .dots_b::before {content:''; position: absolute; width: 0.4rem; height: 0.4rem; left:-1rem; top:1rem; background-color: #222222;}
.portfolio_info .cont_2 .txt_box ul li:not('.target_move') {flex-basis:100%;}
.portfolio_info .cont_2 .txt_box .useing_code {flex-wrap: wrap; margin-top:1rem;}
.portfolio_info .cont_2 .txt_box .useing_code li {margin:0 10px; font-size:2.1rem;}
.portfolio_info .link_box {display:flex; margin:2rem 0 3rem;}
.portfolio_info .link_box .target_move a {background:#ffffff; border:0.1rem solid #dddddd; border-radius:0.5rem; font-weight: 900; padding:1rem; color:#222222; transition: all 0.2s;}
.portfolio_info .link_box .target_move a:hover {background:#757575; border-color:#757575; color:#eeeeee;}
.portfolio_info .link_box .target_move a span {transition: all 0.2s;}
.portfolio_info .link_box .target_move a:hover span {color:#eeeeee;}
.portfolio_info .img_box.hide {opacity: 0; transform:translateY(-1rem); transition: opacity 0.2s, transform 0.2s;}
.portfolio_info .img_box.show {opacity: 1; transform:translateY(0); transition: opacity 0.2s, transform 0.2s;}

/* reserve */
.reserve {padding-bottom:2rem; background:#222222; z-index: 1;}
.reserve * {color:#dddddd;}
.reserve .color_box.c_f6f6ef {background: #f6f6ef !important;}
.reserve .color_box.c_2c2a29 {background: #2c2a29 !important;}
.reserve .color_box.c_222222 {background: #222222 !important;}
.reserve .color_box.c_976d3f {background: #976d3f !important;}
.reserve .color_box.c_eeeeee {background: #eeeeee !important;}
.reserve .page_tit {color:#976d3f; font-family: 'NotoKr';}
.reserve .cont_box {margin-bottom:3rem;}
.reserve .cont_box ul li {word-break: keep-all; line-height: 2;}
.reserve .cont_box .img_box {position:absolute; z-index: -1;}
.reserve .cont_box .sub_tit {font-size:2.4rem; margin-bottom:1rem;}
.reserve .cont_1 {z-index:1; padding-top:5rem; padding-bottom:5rem;}
.reserve .cont_1 .img_1 {width:45%; top:3rem; left:5%;}
.reserve .cont_1 .img_1 img {width:100%; opacity: 0.8;}

.reserve .cont_2 .sub_txt ul li span {font-size:1.4rem; font-weight: 600;}
.reserve .cont_2 .sub_txt ul li .img_box {position:relative; width: 100%; margin-top:2rem; margin-bottom:2rem; text-align: center;}
.reserve .cont_2 .sub_txt ul li .img_box img {width:70%;}
.reserve .cont_2 .img_box .sub_txt {font-size: 1.4rem; font-weight: 600;}
.reserve .cont_2 .img_box .sub_txt span {width:100%;}

.reserve .cont_3 .sub_txt ul:not(.use_colors) li .color_box {display:inline-block; width: 2rem; height:2rem; margin-right:0.8rem; border:0.1rem solid #efefef; background: #ffffff;}
.reserve .cont_3 .use_colors {justify-content: space-around;}
.reserve .cont_3 .use_colors li {display:flex; flex-direction: column; align-items: center;}
.reserve .cont_3 .use_colors .popup.open_pop {opacity: 1;}
.reserve .cont_3 .use_colors .color_box {width:5rem; height:5rem; margin-top:2rem; border:0.2rem solid #bcbcbc; cursor: pointer;}

.reserve .cont_4 .sub_txt ul li {font-size:2rem; margin: 0 1rem;}

.reserve .page_alert {display:flex; align-items:center; padding:1rem 0; margin:1rem 0; font-size:1.8rem; letter-spacing:-0.15rem; word-break: keep-all;}
.reserve .page_alert span {margin-right:1.5rem;}
.reserve .page_alert:first-child {border:0.1rem solid #ffffff;}

/* clean */
.clean {font-family: 'Pretendard'; letter-spacing: -1px;}
.clean * {color:#222222;}
.clean .cont_1 .sub_tit {margin-bottom:1rem; font-size:2rem;}
.clean .cont_1 .sub_txt {line-height: 1.5;}
.clean .cont_1 .sub_txt p {font-size:1.7rem;}
.clean .cont_2 {margin-top:2rem;}
.clean .cont_2 h2 {font-size:1.7rem;}
.clean .cont_2 .img_box {margin-top:1.5rem;}
.clean .cont_2 .img_box img {width:90%; border:0.1rem solid #dddddd;}


/* acrc */
.acrc {font-family: 'Pretendard'; letter-spacing: -1px;}
.acrc * {color:#222222;}
.acrc .cont_1 .sub_txt {line-height: 1.5;}
.acrc .cont_2 .img_box img {width:90%; border:0.1rem solid #dddddd;}

/* child */
.child {font-family: 'Pretendard'; letter-spacing: -1px;}
.child * {color:#222222;}
.child .cont_1 .sub_txt {line-height: 1.5;}
.child .cont_2 .img_box img {width:90%; border:0.1rem solid #dddddd;}

/* prtl */
.prtl {font-family: 'Pretendard'; letter-spacing: -1px;}
.prtl * {color:#222222;}
.prtl .cont_1 .sub_txt {line-height: 1.5;}
.prtl .cont_2 .img_box img {width:90%; border:0.1rem solid #dddddd;}

/* iPad Air */
@media screen and (max-width:1180px) {
    #section_02 .page_tit {width:50%;}
    #section_02 .cont_box .img_box {background-position: 50%;}
    #section_02 .cont_box .txt_box.intro {padding-top:23rem; margin-left:33%;}
    #section_02 .cont_box .txt_box.intro .sub_tit {padding-left:30rem; padding-bottom:1rem; margin-top:3rem;}
}
@media screen and (max-width:820px) {
    /* section_02 */
    #section_02 .cont_box .img_box {background-position:65%;}

    /* section_03 */
    #section_03 .cont_box .web_work li {flex-basis: calc(100%/3);}
}

